import { useState } from 'react'
import './App.css';
import cars from './images/cars.png'
import tv from './images/tv2.svg'
import flies from './images/flies.png'
import roz from './images/roz.png'
import remote from './images/remote.svg'
import game from './images/game.png'
import codebreakers from './images/codebreakers.png'
import minewsweeper from './images/minesweep.png'
import visualiser from './images/visualiser.png'
import bookmark from './images/bookmark.png'
import quizzo from './images/quizzo.png'
import cryptex from './images/cryptex.png'
import gitHub from './images/github.svg'
import RepairShop from './RepairShop';
import WiP from './WIP';

function App() {

  document.title = 'Craig McLeod | Dev Portfolio'
  const [current, setCurrent] = useState(0)
  const [apps] = useState([
    { img: roz, url: 'https://www.rosalinderskine.com/', git: false }, 
    { img: quizzo, url: 'https://baypluzz.herokuapp.com/', git: 'https://github.com/crmcleod/poll_quiz_generator' }, 
    { img: codebreakers, repair: true, git: 'https://github.com/crmcleod/CodeBreakers_game' }, 
    { img: cars, url: 'https://crmcleod.github.io/wheelspin/' , git: 'https://github.com/crmcleod/wheelspin'}, 
    { img: bookmark, url: 'https://bookmarko-client.herokuapp.com/', git: false }, 
    { img: minewsweeper, url: 'https://craigmcleod2i.github.io/dinecreeper/' , git: 'https://github.com/CraigMcleod2i/dinecreeper'}, 
    { img: visualiser, url: 'https://crmcleod.github.io/algorithm-visualisation/', git: 'https://github.com/crmcleod/algorithm-visualisation' }, 
    { img: game, wip: true, git: false }, 
    { img: flies, url: 'https://crmcleod.github.io/flies_down/' , git: 'https://github.com/crmcleod/flies_down'}, 
    { img: cryptex, url: 'https://craigmcleod2i.github.io/cryptex_code/', git: 'https://github.com/CraigMcleod2i/cryptex_code' }
  ])
  const [repairShop, setRepairShop] = useState(false)
  const [WIP, setWIP] = useState(false)

  const handleGo = () => {
    apps[current]?.url && window.open(apps[current].url, '_blank')
    apps[current]?.repair && setRepairShop(true)
    apps[current]?.wip && setWIP(true)
    // apps[current]?.wip && setWIP(true)
  }

  const handleBack = () => {
    setRepairShop(false)
    setWIP(false)
  }

  return (
    <div className="App">
      {repairShop && <RepairShop goBack={handleBack}/>}
      {WIP && <WiP goBack={handleBack} />}
      <div id='tv-wrapper'>
        <img key={current} src={apps[current].img} id='cars'/>
        <div id='tv'>
        <img src={tv} />
        <a style={{cursor: `${apps[current].git ? 'pointer' :'not-allowed'}`}} id='github-logo' href={apps[current].git} rel='noreferrer' target='_blank'>
          <img src={gitHub}/>
        </a> 
        </div>
      </div>
      <div id='remote'>
        <img src={remote} />
        <div id='remote-buttons'>
          <button className='tv-button' onClick={() => setCurrent(0)}></button>
          <button className='tv-button' onClick={() => setCurrent(1)}></button>
          <button className='tv-button' onClick={() => setCurrent(2)}></button>
          <button className='tv-button' onClick={() => setCurrent(3)}></button>
          <button className='tv-button' onClick={() => setCurrent(4)}></button>
          <button className='tv-button' onClick={() => setCurrent(5)}></button>
          <button className='tv-button' onClick={() => setCurrent(6)}></button>
          <button className='tv-button' onClick={() => setCurrent(7)}></button>
          <button className='tv-button' onClick={() => setCurrent(8)}></button>
          <button className='tv-button' onClick={() => setCurrent(9)}></button>
          <button onClick={handleGo} id='go-button' style={{ width: '80%' }}>GO!</button>
        </div>
      </div>

    </div>
  );
}

export default App;
